<template>
  <div v-if="movie">
    <v-card class="mx-8 my-12" fluid>
      <template slot="progress">
        <v-progress-linear
          color="deep-purple"
          height="10"
          indeterminate
        ></v-progress-linear>
      </template>

      <v-img height="250" v-bind:src="movie.Poster"></v-img>

      <v-card-title>{{ movie.Title }}</v-card-title>

      <v-card-text>
        <v-row align="center" class="mx-0">
          <v-rating
            :value="Number(movie.imdbRating)"
            color="amber"
            dense
            half-increments
            readonly
            size="14"
            :length="10"
          ></v-rating>

          <div class="grey--text ms-4">
            {{ movie.imdbRating }} ({{ movie.imdbVotes }})
          </div>

          <div class="text-center mx-8">
            <v-dialog v-model="dialog" width="500">
              <template v-slot:activator="{ on, attrs }">
                <v-btn color="red lighten-2" dark v-bind="attrs" v-on="on">
                  View Rating
                </v-btn>
              </template>

              <v-card>
                <v-card-title class="text-h5 grey lighten-2">
                  Rating and Review
                </v-card-title>

                <v-card-text>
                  <v-card v-for="review in movie.Ratings" :key="review.Source" class="p-3">
                    <h3>sds</h3>
                  </v-card>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" text @click="dialog = false">
                    I accept
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </div>
        </v-row>

        <div class="my-4 text-subtitle-1">
          {{ movie.Actors }}
        </div>

        <div>
          {{ movie.Plot }}
        </div>
      </v-card-text>

      <v-card-text>
        <v-chip-group
          active-class="deep-purple accent-4 white--text"
          column
        >
          <v-chip v-for="genre in movie.Genre.split(', ')" v-bind:key="genre">{{
            genre
          }}</v-chip>
        </v-chip-group>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "Detail",
  data() {
    return {
      movie: null,
      dialog: false,
    };
  },
  mounted: function() {
    const imdbID = this.$route.params.imdbID;

    fetch(`https://www.omdbapi.com/?i=${imdbID}&apikey=87d10179`)
      .then((response) => response.json())
      .then((data) => {
        this.movie = data;
      });
  },
};
</script>
