<template>
  <div class="home">
    <v-container>
      <Search v-on:callApi="callOmdbApi" />
      <ListItem
        v-for="movie in movies"
        v-bind:key="movie.imdbID"
        v-bind:movie="movie"
      />
    </v-container>
  </div>
</template>

<script>
import Search from "@/components/Search.vue";
import ListItem from "@/components/ListItem.vue";

export default {
  name: "Home",
  components: {
    Search,
    ListItem,
  },
  data() {
    return {
      movies: []
    };
  },
  methods: {
    callOmdbApi: function(searchedMovie) {
      fetch(`https://www.omdbapi.com/?s=${searchedMovie}&apikey=87d10179`)
        .then((response) => response.json())
        .then((data) => this.movies = data.Search);
    }
  },
};
</script>
